:root {
  --text: #ddd;
}

body {
  margin: 0;
  background-color: #2e2e2e;
  color: var(--text);
  font-size: 26px;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  font-size: 1.5rem;
}

ul {
  list-style-image: url(../images/sword.svg);
}

.hero {
  background: url("../images/swc-banner.jpg");
  background-size: cover;
  background-position: center;
  min-height: 300px;
  width: 100%;
}