@use "variables" as var;

body {
  margin: 0;
  background-color: var.$bg;
  color: var(--text);
  font-size: 26px;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  font-size: 1.5rem;
}

ul {
  list-style-image: url(../images/sword.svg);
}
